import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { path: '/', redirect: '/index.html' },
    {
      path: '/index',
      component: () => import('./views/Index.vue'),
      children:[
        {path:"/index.html",component: () => import('./views/Index_.vue')},
        {path:"/user.html",component: () => import('./views/UserCenter.vue')},
        {
          path:"/questions/add.html",
          name:'qes-add',
          redirect: '/question/one/add.html',
          component: () => import('./views/questions/Question-add.vue'),
          children:[
            {
              path: '/questions/one/add.html',
              name: 'one-add',
              redirect: '/question/one/choose/add.html',
              component: () => import('./views/questions/Question-one-add.vue'),
              children: [
                {
                  path:'/question/one/choose/add.html',
                  name:'qes-choose',
                  component: () => import('./views/questions/Question-add-single.vue')
                }
              ]
            },
            {
              path:'/questions/more/add.html',
              name:'more-add',
              component: () => import('./views/questions/Question-more-add.vue')
            }
          ]
        },
      ]
    },
    { path: '/login.html', component: () => import('./views/Login.vue') },
    { path: '/register', component: () => import('./views/Register.vue') },
    { path: '/retrieve/pwd', component: () => import('./views/Retrieve-pwd.vue') }
  ],
})
