import qs from 'qs'
import axios, {AxiosRequestConfig, AxiosPromise} from 'axios'
import {Message} from 'element-ui'

const cookies = {
    get: function (name) {
        if (document.cookie.length>0){
            let start=document.cookie.indexOf(name + "=")
            if (start!=-1){
                start=start + name.length + 1
                let end=document.cookie.indexOf(";",start)
                if (end==-1){
                    end=document.cookie.length
                }
                return unescape(document.cookie.substring(start,end))
            }
        }
        return ""
    },
    set: function (name, value, expire=1800) {
        if (name == null){
            return
        }
        let date = new Date()
        date.setSeconds(date.getSeconds()+expire)
        document.cookie=name+ "="+escape(value)+"; expires="+date.toUTCString();
    },
    update: function (name, value=undefined) {
        if (value == undefined){
            value = this.get(name);
        }
        cookies.set(name, value);
    },
    remove: function (name) {
        cookies.set(name, "", -1);
    },
    clear: function () {
        let key = document.cookie.match(/[^ =;]+(?==)/g);
        if(key!=null){
            key.map(value => {
                cookies.set(value,"", -1);
            });
        }
    }
}

let ajax = {
    send: function<T = AxiosPromise>(config: AxiosRequestConfig){
        if (config != null){
            config.data = qs.stringify(config.data);
        }
        return axios(config);
    },
    post:function<T = AxiosPromise>(url, data, config:AxiosRequestConfig) {
        if (config != null){
            config.data = qs.stringify(config.data);
        }
        return axios.post(url, config.data, config)
    },
    get: function<T = AxiosPromise>(url, config:AxiosRequestConfig) {
        if (config != null){
            config.data = qs.stringify(config.data);
        }
        return axios.get(url, config)
    }
}


axios.interceptors.request.use(config=> {
    return config;
}, err=> {
    Message.error('请求超时!');
    return Promise.resolve(err);
} )

axios.interceptors.response.use(data=> {
    if (data.status && data.status == 200 && data.data.status == 'error') {
        Message.error(data.data.msg);
        return;
    }
    return data.data;
}, err=> {
    if (err.response == null) {
        Message.error('网络错误!');
    }
    return Promise.resolve(err);
})

export {
    ajax,
    cookies
}
